import { Directive, Input, ElementRef, HostBinding } from '@angular/core';
@Directive({
    selector: '[farris-col]'
})
export class FarrisColDirective {
    // col
    @Input() col: boolean;
    @Input('col-sm') colSm: boolean;
    @Input('col-md') colMd: boolean;
    @Input('col-lg') colLg: boolean;
    @Input('col-xl') colXl: boolean;
    @Input() span: number = 0;
    // åå³ç§»å¨å ä¸ªcol
    @Input() offset: number = 0;
    // order colé¡ºåº
    @Input() order: number;
    // <576
    @Input() xs: any;
    // >=576
    @Input() sm: any;
    // >=768
    @Input() md: any;
    // >=960
    @Input() lg: any;
    // >=1200
    @Input() xl: any;
    @HostBinding('class')
    get ColClass() {
        return `${this.nativeClass} ${this.getSpanClass()}${this.getBreakpointClass()}${this.getOffsetClass()}${this.getOrderClass()}`;
    }
    @HostBinding('class.col')
    get isCol() {
        return this.col;
    }
    @HostBinding('class.col-sm')
    get isColSm() {
        return this.colSm;
    }
    @HostBinding('class.col-md')
    get isColMd() {
        return this.colMd;
    }
    @HostBinding('class.col-lg')
    get isColLg() {
        return this.colLg;
    }
    @HostBinding('class.col-xl')
    get isColXl() {
        return this.colXl;
    }
    nativeClass = '';
    constructor(private el: ElementRef) {
        this.nativeClass += this.el.nativeElement.classList.value;
    }
    /* è·åspan ç±»å */
    getSpanClass() {
        return this.span ? ` col-${this.span}` : ` `;
    }
    /* è·åååºå¼ ç±»å  xs sm md lg xl å¯ä»¥æ¯æ°å­ï¼ä»£è¡¨span;ä¹å¯ä»¥æ¯å¯¹è±¡ï¼åé¨åå«span offset*/
    getBreakpointClass() {
        const makeClass = (breakpoint: string): string => {
            const props = this[breakpoint] || {};
            // è·åå¯¹è±¡ä¸­  spanæèoffset
            return Object.keys(props).map(prop => prop === 'span' ?
                `col-${breakpoint}-${props[prop]}` : prop === 'offset' ? `offset-${breakpoint}-${props[prop]}` :
                    `order-${breakpoint}-${props[prop]}`).join(' ');
        };
        return ['sm', 'md', 'lg', 'xl'].reduce((pre, current) => {
            return typeof this[current] === 'object' ? `${pre} ${makeClass(current)}` :
                typeof this[current] === 'number' ? `${pre} col-${current}-${this[current]}` : `${pre} `;
        }, '');
    }
    /* è·åoffset */
    getOffsetClass() {
        return this.offset ? ` offset-${this.offset}` : ` `;
    }
    // get order class
    getOrderClass() {
        return this.order ? ` order-${this.order}` : ` `;
    }
}
