import { Directive, Input, ElementRef, Host, HostBinding, OnInit, Renderer2 } from '@angular/core';
@Directive({
    selector: '[farris-container]'
})
export class FarrisContainerDirective implements OnInit {
    @HostBinding('class.container') isContainer = true;
    // å¯ä»¥è®¾ç½®å®½åº¦åé«åº¦
    // è®¾ç½®å®½ é«å±æ§
    _width: string | number;
    _height: string | number;
    @Input()
    get width() {
        // å¯¹åä¸ªåä½è¿è¡å¤ç
        return this._width;
    }
    set width(width: any) {
        this._width = this.resolveSize(width);
    }
    @Input()
    get height() {
        return this._height;
    }
    set height(height: any) {
        this._height = this.resolveSize(height);
    }
    constructor(private el: ElementRef, private render: Renderer2) {
    }
    ngOnInit() {
        // å®½é«  èµå¼
        this.render.setStyle(this.el.nativeElement, 'width', this.width);
        this.render.setStyle(this.el.nativeElement, 'height', this.height);
    }
    /*å®½é«ç±»åæ¯ stringæènumber è§£æå®½é«  å°ºå¯¸ */
    resolveSize(size) {
        const regex = /px|em|rem|pt|%/;
        // è¯´ææ¯å­ç¬¦ä¸²
        return regex.test(size) ? `${parseInt(size, 10)}${size.match(regex)[0]}` : `${size}px`;
    }
}
