import { Directive, Input, ElementRef, HostBinding, OnInit } from '@angular/core';
@Directive({
    selector: '[farris-row]',
})
export class FarrisRowDirective implements OnInit {
    @Input() type: string = 'flex';
    @Input() gutter: number = 16;
    @Input() justify: string = 'start';
    @Input() align: string = 'start';
    /**
     * xs={
     * justify:'start',
     * align:'start'
     * }
     */
    @Input() xs: any;
    @Input() sm: any;
    @Input() md: any;
    @Input() lg: any;
    @Input() xl: any;
    @HostBinding('class')
    get respClass() {
        return `${this.getJustifyClass()}${this.getAlignClass()}${this.getGutterClass()}${this.getBreakpointClass()}`;
    }
    // ç»å®row å°class
    @HostBinding('class.row') isRow = this.type === 'flex';
    //
    nativeClass: string = '';
    constructor(private el: ElementRef) {
        // åå§åç±»å æ·»å å®¿ä¸»åç´ å·²æçç±»å
        this.nativeClass += this.el.nativeElement.classList.value;
    }
    ngOnInit() {
    }
    // è·åjustify class
    getJustifyClass() {
        const justifyStr = this.justify !== 'start' ? ` justify-content-${this.justify}` : ' ';
        return `${this.nativeClass}${justifyStr}`;
    }
    // è·å align class
    getAlignClass() {
        return this.align !== 'start' ? ` align-items-${this.align}` : ' ';
    }
    // ååºå¼ justify
    getBreakpointClass() {
        const makeClass = (breakpoint: string): string => {
            const props = this[breakpoint] || {};
            // è·åå¯¹è±¡ä¸­  justifyæèalign
            return Object.keys(props).map(prop => prop === 'justify' ?
                `justify-content-${breakpoint}-${props[prop]}` : `align-items-${breakpoint}-${props[prop]}`).join(' ');
        };
        const classStr = ['sm', 'md', 'lg', 'xl'].reduce((pre, current) => {
            return `${pre} ${makeClass(current)}`;
        }, '');
        return classStr;
    }
    // ææ é´é
    getGutterClass() {
        return !this.gutter ? ' no-gutters' : ' ';
    }
}
